                   <span class='text-code'>changeView</span><span class='text-code'>Boolean</span><span class='text-code'>changeView</span><span class='text-code'>Boolean</span>DialogDialog <span class='text-code'></span><span class='text-code'></span><span class='text-code'></span><span class='text-code'>content</span><span class='text-code'>slot</span><span class='text-code'></span><span class='text-code'>slot</span>
