                <y-dialog :changeView="value2" :before-close='close'>
                    before-close
                    <template v-slot:content>
                        <input type="text" v-model='text1'  placeholder="">
                        <strong class="content-text">dialog'close'</strong>                       
                    </template>
                </y-dialog>
            </template>
            <template v-slot:description>
                <div>
                    <span class='text-code'>before-close</span>Dialog<span class='text-code'>before-close</span><span class='text-code'>true</span>
                </div>
            </template>
            <template v-slot:code>
                 <div>
                    <pre><code v-html="prism.highlight(code2, prism.languages.html, 'html')">
                        
</code></pre>
                </div>
            </template>
        </demo-block>
        <demo-block>
            <h3>before-cancel</h3>
            <template v-slot:source>
                <y-button @click="value3 = !value3" level='main' circle>Dialog</y-button>
                <y-dialog :changeView="value3" :before-cancel='cancel'>
                    before-cancel
                    <template v-slot:content>
                        <input type="text" v-model='text2'  placeholder="">
                        <strong class="content-text">dialog'cancel'</strong>
                    </template>
                </y-dialog>
            </template>
            <template v-slot:description>
                <div>
                    <span class='text-code'>before-cancel</span>Dialog<span class='text-code'>before-cancel</span><span class='text-code'>true</span>
                </div>
            </template>
            <template v-slot:code>
                 <div>
                    <pre><code v-html="prism.highlight(code3, prism.languages.html, 'html')">
                        
