            <template v-slot:source>
                <y-button @click="value4 = !value4" level='main' circle>Dialog</y-button>
                <y-dialog :changeView="value4" :before-ensure='ensure'>
                    before-ensure
                    <template v-slot:content>
                        <input type="text" v-model='text3'  placeholder="">
                    <strong class="content-text">dialog'ensure'</strong>
                    </template>
                </y-dialog>
