                <div>
                    Dialog<span class='text-code'>z-index</span><span class='text-code'>10</span><span class='text-code'>z-index</span>Dialog<span class='text-code'>z-index</span>Dialog<span class='text-code'>z-index</span>
                </div>
            </template>
            <template v-slot:code>
                 <div>
                    <pre><code v-html="prism.highlight(code5, prism.languages.html, 'html')">
                        
