</template>
<script>
import Dialog from '../lib/Dialog.vue'
import Button from '../lib/Button.vue'
import DemoBlock from './DemoBlock.vue'
import 'prismjs'
const prism = window.Prism
import 'prismjs/themes/prism.css'
export default {
    components: {
        'y-dialog': Dialog,
        'y-button': Button,
        'demo-block': DemoBlock
    },
    data(){
        return {
            value1: false,
            value2: false,
            value3: false,
            value4: false,
            value5: false,
            value6: false,
            text1: undefined,
            text2: undefined,
            text3: undefined,
            prism: prism,
            code1:"<y-button @click='value = !value' level='main' circle>Dialog</y-button>\n<y-dialog :changeView='value'>\n    \n    <template v-slot:content>\n        \n    </template>\n</y-dialog><script>\n    export default {\n        data(){\n            return {\n                value: false\n            }\n        }\n    }\n</"+"script>",
            code2:"<y-button @click='value = !value' level='main' circle>Dialog</y-button>\n<y-dialog :changeView='value' :before-close='close'>\n    before-close\n    <template v-slot:content>\n        <input type='text' v-model='str'  placeholder=''>\n        <strong>\n            dialog'close'\n        </strong>\n    </template>\n</y-dialog>\n<script>\n    export default {\n        data(){\n            return {\n                value: false,\n                str: undefined\n            }\n        },\n        methods: {\n            close(){\n               if(this.str !== 'close'){\n                    alert(''close'')\n                }else{\n                    return true\n                }            \n            }\n        }\n    }\n</"+"script>",
            code3:"<y-button @click='value = !value' level='main' circle>Dialog</y-button>\n<y-dialog :changeView='value' :before-cancel='cancel'>\n    before-cancel\n    <template v-slot:content>\n        <input type='text' v-model='str'  placeholder=''>\n        <strong>\n            dialog'cancel'\n        </strong>\n    </template>\n</y-dialog>\n<script>\n    export default {\n        data(){\n            return {\n                value: false,\n                str: undefined\n            }\n        },\n        methods: {\n            cancel(){\n               if(this.str !== 'cancel'){\n                    alert(''cancel'')\n                }else{\n                    return true\n                }            \n            }\n        }\n    }\n</"+"script>",
            code4:"<y-button @click='value = !value' level='main' circle>Dialog</y-button>\n<y-dialog :changeView='value' :before-ensure='ensure'>\n    before-ensure\n    <template v-slot:content>\n        <input type='text' v-model='str'  placeholder=''>\n        <strong>\n            dialog'ensure'\n        </strong>\n    </template>\n</y-dialog>\n<script>\n    export default {\n        data(){\n            return {\n                value: false,\n                str: undefined\n            }\n        },\n        methods: {\n            cancel(){\n               if(this.str !== 'ensure'){\n                    alert(''ensure'')\n                }else{\n                    return true\n                }            \n            }\n        }\n    }\n</"+"script>",
            code5:"<y-button @click='value1 = !value1' level='main' circle></y-button>\n<y-button @click='value2 = !value2' level='main' circle></y-button>\n<y-dialog :changeView='value1'>\n    \n    <template v-slot:content>\n        \n    </template>\n</y-dialog>\n<y-dialog :changeView='value2' z-index='200'>\n    \n    <template v-slot:content>\n        \n    </template>\n</y-dialog>\n<script>\n    export default {\n        data(){\n            return {\n                value1: false,\n                value2: false\n            }\n        }\n    }\n<"+"/script>"
        }
    },
    watch: {
        num: function(){
            console.log(this.num)
        }
    },
    methods: {
        close(){
            if(this.text1 !== 'close'){
                alert('"close"')
            }else{
                return true
            }            
        },
        cancel(){
            if(this.text2 !== 'cancel'){
                alert('"cancel"')
            }else{
                return true
            }  
        },
        ensure(){
            if(this.text3 !== 'ensure'){
                alert('"ensure"')
            }else{
                return true
            }
        }
    }
}
</script>
